html, body, .root {
    height: 100%;
}
body {
    line-height: 1.5;
}
body, .app {
    background: white;
}

/* main page */

.list-books {
    max-width: 85vw;
    margin: auto;
}

.list-books-title {
    padding: 10px 0;
    background: #2e7c31;
    text-align: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
}

.list-books-title h1 {
    margin: 0;
    margin-left: calc(100vw - 100%);
    font-weight: 400;
    color: white;
}

.list-books-content {
    padding: 0 0 80px;
    -ms-flex: 1 1;
        flex: 1 1;
}

.bookshelf {
    padding: 0 10px 20px;
}

@media (min-width: 600px) {
    .bookshelf {
        padding: 0 20px 40px;
    }
}

.bookshelf-title {
    border-bottom: 1px solid #dedede;
}

.bookshelf-books {
    text-align: center;
    padding: 0 40px;
}

.open-search {
    position: fixed;
    height: 50px;
    width: 100%;
    max-width: inherit;
    bottom: 25px;
}
.open-search a {
    position: absolute;
    right: -25px;
    display: block;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #2e7d32;
    background-image: url('./icons/add.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 28px;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
            box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    font-size: 0;
}

/* search page */

.search-books-bar {
    position: fixed;
    width: 100%;
    top: 68px;
    left: 0;
    z-index: 5;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
            box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}
.search-books-input-wrapper {
    -ms-flex: 1 1;
        flex: 1 1;
    background: #e9e;
}
.search-books-bar input {
    width: 100%;
    padding: 15px 10px;
    font-size: 1.25em;
    border: none;
    outline: none;
}

.close-search {
    display: block;
    top: 20px;
    left: 15px;
    width: 50px;
    height: 53px;
    background: white;
    background-image: url('./icons/arrow-back.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 28px;
    font-size: 0;
}

.search-books-results {
    padding: 80px 10px 20px;
}

/* books grid */

.books-grid {
    list-style-type: none;
    padding: 0;
    margin: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.books-grid li {
    padding: 10px 15px;
    text-align: left;
}

.book {
    width: 140px;
}

.book-title,
.book-authors {
    font-size: 0.8em;
}

.book-title {
    margin-top: 10px;
}

.book-authors {
    color: #999;
}

.book-top {
    position: relative;
    height: 200px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
}

.book-shelf-changer {
    position: absolute;
    right: 0;
    bottom: -10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #60ac5d;
    background-image: url('./icons/arrow-drop-down.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
            box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.book-shelf-changer select {
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.book-shelf-changer select option:first-child {
    background: #2e7c31;
    color: white;
    font-weight: bold;
}

option:checked {
    background: rgba(144, 238, 144, 0.5);
    font-weight: bold;
}

/* book cover */

.book-cover {
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
            box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    background: #eee;
}

.no-thumbnail {
    background-image: url('./icons/no-thumbnail.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.book-cover-title {
    padding: 20px 10px 0;
    text-align: center;
    font-size: 0.8em;
}



